.store {
	display : -ms-flexbox;
	display : flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

@-webkit-keyframes dissappearStore {
	0% {
	    opacity: 1;
	    -webkit-transform: scale(1);
	    transform: scale(1);
	}

	100% {
	    opacity: 0;
			-webkit-transform: scale(1.1);
			transform: scale(1.1);

	}
}

@keyframes dissappearStore {
	0% {
	    opacity: 1;
	    -webkit-transform: scale(1);
	    transform: scale(1);
	}

	100% {
	    opacity: 0;
			-webkit-transform: scale(1.1);
			transform: scale(1.1);

	}
}

@-webkit-keyframes animateStore {
	0% {
	    opacity: 0;
	    -webkit-transform: scale(1.1);
	    transform: scale(1.1);
	}

	100% {
	    opacity: 1;
	    -webkit-transform: scale(1);
	    transform: scale(1);

	}
}

@keyframes animateStore {
	0% {
	    opacity: 0;
	    -webkit-transform: scale(1.1);
	    transform: scale(1.1);
	}

	100% {
	    opacity: 1;
	    -webkit-transform: scale(1);
	    transform: scale(1);

	}
}

.leaving {
	-webkit-animation: dissappearStore 600ms forwards;
	animation: dissappearStore 600ms forwards;
	-webkit-animation-name: dissappearStore;
	        animation-name: dissappearStore;
	-webkit-animation-duration: 600ms;
	        animation-duration: 600ms;
	-webkit-animation-timing-function: initial;
	        animation-timing-function: initial;
	-webkit-animation-delay: initial;
	        animation-delay: initial;
	-webkit-animation-iteration-count: initial;
	        animation-iteration-count: initial;
	-webkit-animation-direction: initial;
	        animation-direction: initial;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-play-state: initial;
	        animation-play-state: initial;
}

.entering-store {
	/*width : 0px !important;*/
	height : 0px !important;
	-webkit-animation: animateStore 900ms forwards;
	animation: animateStore 900ms forwards;
	-webkit-animation-name: animateStore;
	        animation-name: animateStore;
	-webkit-animation-duration: 900ms;
	        animation-duration: 900ms;
	-webkit-animation-timing-function: initial;
	        animation-timing-function: initial;
	-webkit-animation-delay: initial;
	        animation-delay: initial;
	-webkit-animation-iteration-count: initial;
	        animation-iteration-count: initial;
	-webkit-animation-direction: initial;
	        animation-direction: initial;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-play-state: initial;
	        animation-play-state: initial;
}


.item-display {
	margin: 0em 2em;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
	padding-top: 4vh;
}

.storelogo {
	border-radius: 0.2em;
	max-width: 12em;
	max-height: 9em;
	margin: auto;
	width: 50%;
	padding-top: 4vh;
	float : right;
}



