#logo {
	padding-top : 1em;
	padding-bottom: 1em;
	display: inline-block;
	background-position: center;
	background-repeat: no-repeat;
}

.h2:hover {
	cursor: pointer !important;
}

svg {
	max-height: 15px;
	max-width: 15px;
	z-index: 4;

}

#logo div {
	border : solid 1px pink !important;
}

#logo h2 {
	color : white !important;
}


