body, html {
	font-family: 'Nunito', sans-serif !important;
	background-color: #141414 !important;
	background-color: hsl(217, 71%, 53%) !important;
	height: 100%;
  width:100%;
}

.control {
	padding-bottom: 1em;
}

.label {
	color : white;
}

.suggestionForm .box {
	margin-right: 2em !important;
	background-color: grey !important;
	min-width: 30vw;
}

@-webkit-keyframes growForm {

		0% {
			max-width: 0px;
			opacity: 0;
		}

		100% {
			max-width:600px;
			opacity: 1;
		}
}

@keyframes growForm {

		0% {
			max-width: 0px;
			opacity: 0;
		}

		100% {
			max-width:600px;
			opacity: 1;
		}
}


.suggested {
	-webkit-animation: growForm 600ms reverse;
	animation: growForm 600ms reverse;
	-webkit-animation-name: growForm;
	        animation-name: growForm;
	-webkit-animation-duration: 600ms;
	        animation-duration: 600ms;
	-webkit-animation-timing-function: initial;
	        animation-timing-function: initial;
	-webkit-animation-delay: initial;
	        animation-delay: initial;
	-webkit-animation-iteration-count: initial;
	        animation-iteration-count: initial;
	-webkit-animation-direction: reverse;
	        animation-direction: reverse;
	-webkit-animation-fill-mode: reverse;
	        animation-fill-mode: reverse;
	-webkit-animation-play-state: initial;
	        animation-play-state: initial;

}

.suggestionForm {
    -webkit-animation: growForm 600ms forwards;
    animation: growForm 600ms forwards;
    -webkit-animation-name: growForm;
            animation-name: growForm;
    -webkit-animation-duration: 600ms;
            animation-duration: 600ms;
    -webkit-animation-timing-function: initial;
            animation-timing-function: initial;
    -webkit-animation-delay: initial;
            animation-delay: initial;
    -webkit-animation-iteration-count: initial;
            animation-iteration-count: initial;
    -webkit-animation-direction: initial;
            animation-direction: initial;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-play-state: initial;
            animation-play-state: initial;
}

@-webkit-keyframes dissappearShop {
	0% {
	    opacity: 1;
	    -webkit-transform: scale(1);
	    transform: scale(1);
	}

	100% {
	    opacity: 0;
			-webkit-transform: scale(1.1);
			transform: scale(1.1);

	}
}

@keyframes dissappearShop {
	0% {
	    opacity: 1;
	    -webkit-transform: scale(1);
	    transform: scale(1);
	}

	100% {
	    opacity: 0;
			-webkit-transform: scale(1.1);
			transform: scale(1.1);

	}
}

@-webkit-keyframes animateShop {
	0% {
	    opacity: 0;
	    -webkit-transform: scale(1.1);
	    transform: scale(1.1);
	}

	100% {
	    opacity: 1;
	    -webkit-transform: scale(1);
	    transform: scale(1);
	}
}

@keyframes animateShop {
	0% {
	    opacity: 0;
	    -webkit-transform: scale(1.1);
	    transform: scale(1.1);
	}

	100% {
	    opacity: 1;
	    -webkit-transform: scale(1);
	    transform: scale(1);
	}
}

.leaving {
	-webkit-animation: dissappearShop 600ms forwards;
	animation: dissappearShop 600ms forwards;
	-webkit-animation-name: dissappearShop;
	        animation-name: dissappearShop;
	-webkit-animation-duration: 600ms;
	        animation-duration: 600ms;
	-webkit-animation-timing-function: initial;
	        animation-timing-function: initial;
	-webkit-animation-delay: initial;
	        animation-delay: initial;
	-webkit-animation-iteration-count: initial;
	        animation-iteration-count: initial;
	-webkit-animation-direction: initial;
	        animation-direction: initial;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-play-state: initial;
	        animation-play-state: initial;
	z-index: 100;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	justify-content: center;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-direction: column;
	flex-direction: column;
}

.entering {
	-webkit-animation: animateShop 900ms forwards;
	animation: animateShop 900ms forwards;
	-webkit-animation-name: animateShop;
	        animation-name: animateShop;
	-webkit-animation-duration: 900ms;
	        animation-duration: 900ms;
	-webkit-animation-timing-function: initial;
	        animation-timing-function: initial;
	-webkit-animation-delay: initial;
	        animation-delay: initial;
	-webkit-animation-iteration-count: initial;
	        animation-iteration-count: initial;
	-webkit-animation-direction: initial;
	        animation-direction: initial;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	-webkit-animation-play-state: initial;
	        animation-play-state: initial;
	z-index: 100;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	justify-content: center;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-direction: column;
	flex-direction: column;
}

h1 {
	text-align: center !important;
	font-size: 4em !important;
	color : #fff !important;
}

.stores {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	justify-content: center;
	-ms-flex-align: center;
	align-items: center;
	-ms-flex-direction: column;
	flex-direction: row;
}

.li {
	margin-right: 2em !important;
	background-color: grey !important;

}

.landing p {
	color : grey !important;
}

.storeOption:hover p {
	color: white !important;
}

.storeOption {
	padding-right: 2vw !important;
}

.storeOption:hover .box {
	border: 0.001em solid white;
	border-color: #fff !important;
	cursor:pointer;
}
/*
.box {
	max-width: 25vw;
}*/

.storeOption img {
	position: -webkit-sticky !important;
	position: sticky !important;
	border-radius: 0.2em;
	width: 12em;
	height: 4em;
}



