.thumbnail .item {
	min-height: 40vh;
	max-height: 90vh;
	margin-right: 1em;
	margin-left: 1em;
}

p.description {
	padding-bottom: 2em;
}

.thumbnail .card:hover {
	/*outline: 0.4em solid rgba(102, 0, 102, 0.8);*/
	-webkit-box-shadow: 0 0 3pt 4pt rgba(102, 0, 102, 0.7);
	        box-shadow: 0 0 3pt 4pt rgba(102, 0, 102, 0.7);
	cursor:pointer;
}

.thumbnail .card {
	min-height: 80vh;
	max-height: 80vh;
	-webkit-box-shadow: 0 0 6pt 1pt rgba(0, 0, 0, 0.5);
	        box-shadow: 0 0 6pt 1pt rgba(0, 0, 0, 0.5);
}

.thumbnail {
	padding-bottom: 2em;
	-ms-flex-preferred-size: 20rem;
	    flex-basis: 20rem;
}


.thumbnail .content {
	margin-top: -3vh;
	padding : 1em;
}

.thumbnail .buyActions {
	padding-top: 2vh;
	margin-bottom: -1vh;
}

.thumbnail .price {
	font-weight: bold;
	color : white;
	padding : 1em;
	background-color: rgba(0.2, 24, 99, 0.3)
}



