.mainphoto > img {
  display: block;
}

.storeLogo:hover {
	cursor: pointer;
	-webkit-box-shadow: 0 0 3pt 4pt rgba(0, 0, 0, 0.3);
	        box-shadow: 0 0 3pt 4pt rgba(0, 0, 0, 0.3);
}

.price {
	font-weight: bold;
}

.gallery {
	display : -ms-flexbox;
	display : flex;
}

.gallery img {
	width : 2em;
	height : 2em;
	border-radius: 0.03em;
	padding-right: 1vw !important;
}

.gallery img:hover {
	border-radius: 0.001em;
	border-bottom: 0.1em solid purple;
}

.itemPage {
	display : -ms-flexbox;
	display : flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	margin: 1em;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.flex-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}


.selectedPhoto {
	border-bottom: 0.1em solid black;
	border-color:  black !important;
}

.itemPage i {
	font-size: 2em;
}

.itemPage i:hover {
	border: 0.1em solid purple;
	cursor: pointer;
}

.product-description {
	margin-bottom: 1em;
}



